@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  margin: 0;
  padding: 0;
  background: rgb(38, 38, 38);
  font-family: sans-serif;
  overflow-x: hidden;
}
h4 {
  font-size: 1.75rem;
  margin-bottom: 15px;
  margin-top: 50px;
}
h3 {
  font-size: 2rem;
  margin-top: 350px;
  margin-bottom: 50px;
}
h2 {
  font-size: 3rem;
}
h1 {
  margin-top: 200px;
  margin-bottom: 50px;
  font-size: 5rem;
}
img {
  border-radius: 5px;
  width: 300px;
  max-width: 100%;
  max-height: 50%;
  height: auto;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #1e1e2e;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
